 .product {
    display : flex;
    flex-direction: column;
    max-height: 400px;
    min-width: 100px;
    margin: 10px;
    padding: 20px;
    width: 100%;
    z-index: 1;
    background-color: #ffffff;
}

 .product__rating {
     display : flex;
 }

 .product__price {
     margin-top: 5px;
 }
 .product__info {
     height: 100px;
     margin-bottom: 15px;

 }
 .product > button {
     background-color: #f0c14b;
     border: 1px solid;
     border-color: #000000;
 }
 .product > img {
     width : 100%;
     max-height: 200px;
     object-fit: contain;
     margin-bottom: 15px;
 }